}




.nav-main {
    width: 100%;
}

.nav-main__list {
    width: 100%;
    flex: 1;
}

.nav-main__item {
    position: relative;
    color: var(--font);
}
.nav-main>.nav-main__list>.nav-main__item {
    flex: 1 1 auto;
    margin-bottom: 12px;
}


.nav-main__link {
    padding: 5px 4px;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
    color: var(--font);
    font-weight: 400;
}

.nav-main__item:hover > .nav-main__link,
.nav-main__item:hover > .nav-main__icon,
.nav-main__item.current > .nav-main__link,
.nav-main__item.current>.nav-main__icon {
    color: var(--base);
}

.nav-main__item.parent {
    /*padding-right: 24px*/
}

.nav-main__icon {
    font-size: 10px;
    position: absolute;
    top: 11px;
    right: 0px;
}


/* 2 level */
.nav-main__item>.nav-main__list {
    display: none;
    margin: 0;
    background-color: #fff;
    padding: 1em 1em 0 1em;
    text-align: left;
}
.nav-main__item.opened>.nav-main__list {
    display: block;
}
.nav-main__item.opened>.nav-main__icon {
    transform: rotate(180deg);
}

.nav-main__item>.nav-main__list {
    

}

.nav-main__list>.nav-main__item .nav-main__item {
    margin-bottom: 10px;
}
.nav-main__item>.nav-main__list .nav-main__link {
    text-transform: none;
}

.header-bussiness-btn {
    line-height: 24px;
    text-align: center;
    color: var(--font);
    border: 1px solid var(--blue);
    border-radius: 2px;
    padding: 8px 16px;
    min-width: 174px;
    background: #fff;
    text-decoration: none;
}


.mp__overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}

.mp__panel {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 101;
    padding: 0 5px 0 5px;
    text-align: center;
    background: var(--base);
    display: none;
}

.mp__panel:after {
    content: '';
    display: table;
    clear: both;
}

.mp__button-main {
    width: 34px;
    height: 34px;
    margin: 3px;
    background: none;
    border: none;
    padding: 4px;
    border-radius: 3px;
    cursor: pointer;
    outline: none;
    float: right;
}

.mp__line {
    position: relative;
    display: block;
}

.mp__line:after,
.mp__line:before {
    content: "";
    position: absolute;
    left: 0;
    top: -9px;
}

.mp__line:after {
    top: 9px;
}

.mp__line,
.mp__line:after,
.mp__line:before {
    width: 100%;
    height: 2px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 2px;
    background-color: #fff;
}

.mp__button-main.mp--on .mp__line {
    background-color: transparent;
}

.mp__button-main.mp--on .mp__line:before {
    -webkit-transform: rotate(45deg) translate(6px, 7px);
    transform: rotate(45deg) translate(6px, 7px);
}

.mp__button-main.mp--on .mp__line:after {
    -webkit-transform: rotate(-45deg) translate(6px, -7px);
    transform: rotate(-45deg) translate(6px, -7px);
}



.mp__button-text {
    margin: 6px 6px 3px 3px;
    background: none;
    border: solid 1px #fff;
    padding: 6px 4px;
    border-radius: 3px;
    cursor: pointer;
    outline: none;
    font-size: 14px;
    float: left;
    color: #fff;
}

.mp__button-text--center {
    float: none;

}

.mp--hide {
    display: none;
}

.mp__button {
    position: relative;
}

.mp__notification {
    position: absolute;
    top: -5px;
    right: -11px;
    background-color: #dd0000;
    color: #fff;
    padding: 1px 3px;
    border-radius: 10px;
    min-width: 12px;
}



@media screen and (min-width: 576px){

    
}

@media only screen and (max-width : 767px) {

    .mp__overlay.mp--on {
        display: block;
    }

    .mp__panel {
        display: block;
    }

    body.mp--init {
        padding-top: 40px;
    }


    .mp__nav-panel {
        display: block;
        position: fixed;
        top: 0;
        bottom: 0;
        width: 80%;
        max-width: 260px;
        overflow-y: auto;
        -webkit-box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.8);
        -webkit-transition: left, right 0.3s linear;
        box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.8);
        z-index: 102;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        background-color: #fff;
        margin: 0;
        padding: 45px 20px 20px 20px !important;
        height: auto !important;
    }

    .mp__nav-panel_main {
        left: -100%;
        transition: left 0.3s linear;
    }


    .mp__nav-panel_second {
        right: -125%;
        transition: right 0.3s linear;
    }

    .mp__nav-panel_main.mp--on {
        left: 0;
    }


    .mp__nav-panel_second.mp--on {
        right: 0;
    }

}


